body {
	margin: 0;
	padding: 0;
	font-family: raleway, sans-serif;
	--gray: #a4a4a4;
}
/* Change the white to any color ;) */
input:-webkit-autofill,textarea:-webkit-autofill  {
    -webkit-box-shadow: 0 0 0 30px white inset;
}

html,body,#root{
	height:100%;
}


